// @ts-check
/**
 * Sets the defaults for AOM specs
 */
export const name = "fdp/defaults";
import { coreDefaults } from "../core/defaults.js";

const licenses = new Map([
  [
    "fdp",
    {
      name: "Fair Data Point Specification",
      short: "FDP",
      url: "https://www.apache.org/licenses/LICENSE-2.0",
    },
  ],
]);

const fdpDefaults = {
  // treat document as "Common Markdown" (with a little bit of HTML).
  // choice between Markdown and HTML depends on the complexity of the spec
  // example of Markdown spec: https://github.com/WICG/netinfo/blob/gh-pages/index.html
  // Helpful guide: https://respec.org/docs/#markdown
  format: "markdown",
  logos: [
    {
      src: "https://specs.fairdatapoint.org/images/FAIRDataPoint_logo-small.png",
      alt: "FDP",
      id: "FDP",
      height: 170,
      width: 170,
      url: "https://fairdatapoint.org/",
    },
  ],
  license: "fdp",
};

function computeProps(conf) {
  return {
    licenseInfo: licenses.get(conf.license),
  };
}

export function run(conf) {
  // assign the defaults
  const lint =
    conf.lint === false
      ? false
      : {
          ...coreDefaults.lint,
          ...fdpDefaults.lint,
          ...conf.lint,
        };
  Object.assign(conf, {
    ...coreDefaults,
    ...fdpDefaults,
    ...conf,
    lint,
  });

  // computed properties
  Object.assign(conf, computeProps(conf));
}
